import styles from './app.module.css';
import React , { useState, useEffect } from 'react';
import Videolist from './components/video_list/video_list';
import Searchheader from './components/search_header/search_header';
import VideoDetail from './components/video_detail/video_deteail';

function App({youtube}) {
  const [videos, setVideos] = useState([]);
  const [selectedChannel, setChannel] = useState(null);
  const [selectedVideo, setSelectedVideo] = useState(null);

  const selectVideo = (video) => {
    setSelectedVideo(video);
    selectChannel(video.snippet.channelId);    
        
  }

  const selectChannel = (id) =>{
    youtube.channel(id)    
    .then(channel => setChannel(channel[0].snippet.thumbnails.default.url)); 
  }
  const search = query => {    
    setSelectedVideo(null);
    youtube.search(query)
    .then(videos => setVideos(videos));        
  }

  useEffect(()=>{
    youtube.mostPopular()
    .then(videos => setVideos(videos));
  },[youtube]);

  return (
    <div className={styles.app}>
      <Searchheader onSearch={search}/>
      <section className={styles.content}>
      {selectedVideo && <div className={styles.detail}>
          <VideoDetail channel={selectedChannel} video={selectedVideo}/>
        </div>}
        <div className={styles.list}>
          <Videolist key={videos.key} videos={videos} onVideoClick={selectVideo} display={selectedVideo ? 'list' : 'grid'}/>
        </div>
      </section>
    </div>
  );
}

export default App;
